@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  -webkit-tap-highlight-color: transparent;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Open Sans", serif;
}

.wrapper {
  background: linear-gradient(to right, #70bef4, #066ab0);
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  min-height: 100vh;
  background: #fff;
  box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
  width: 100%;
  margin: 0 auto;
  background: url(/weather-app/static/media/background.bc832e40.jpg) center center no-repeat;
  background-size: cover;
}

.form-container {
  height: 100vh;
  width: 100%;
  padding-top: 10%;
  padding-left: 0px;
  align-items: center;
  justify-content: center;
  text-align: center;
}

input[type="text"] {
  background-color: rgba(0,0,0,0.75);
  padding: 2px 5px;
  border: 0;
  border-bottom: solid 1px #f16051;
  width: 85%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  font-size: 1.8em;
}

input[type="text"] { 
    outline: none;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
    -webkit-text-fill-color: #fff !important;
}

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 1.5em;
  cursor: pointer;
  background-color: #49aee9;
  color: #fff;
  font-weight: 100;
}

button:active {
  outline: none;
}

.weather__info {
  font-size: 200px;
  font-weight: 200;
  letter-spacing: 2px;
}

.weather__key {
  color: #f16051;
  border-bottom: solid 2px rgba(255,255,255,0.06);
  padding: 20px 0 20px 0;
  font-weight: 400;
  font-size: 1.2em;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  font-size: 1.5em;
  color: #2c3e50;
  font-weight: 200;
}

.weather__error {
  color: #2c3e50;
  font-size: 2em;
  letter-spacing: 1px;
  font-weight: 200;
}
